.meir-img img {
    max-width: unset;
  }

@media only screen and (max-width: 300px) {
    #PHILOSOPHY {
        min-height: 100vh;
    }


    .popout-1-div,
    .popout-2-div,
    .popout-3-div {
        position: relative !important;
        margin:5vw;
    }

    .popout-1,
    .popout-2,
    .popout-3 {
        display: flex;
        opacity: 1 !important;
    }

    .plus-button-1 svg,
    .plus-button-2 svg,
    .plus-button-3 svg {
        display: none;
    }

    .popout-1-div h4,
    .popout-2-div h4,
    .popout-3-div h4 {
        font-size: 1rem;
    }

    .popout-1-div h5,
    .popout-2-div h5,
    .popout-3-div h5 {
        font-size: 0.75rem;
    }
}

